.postContainer {
    display: block;
}

.postInfo {
    margin-left: 5.5%;
    margin-top: 15%;
    width: 97%;
}

.postImage {
    width: 90%;
}

.scrollContainer {
    display: block;
    margin-top: 1%;
}

.scrollPhoto {
    font-size: 125%;
}

.scrollPhoto:hover {
    cursor: pointer;
}

.postTitle {
    margin-top: 2%;
}

.infoContainer {
    margin-top: 1.5%;
    margin-left: 5.5%;
}

.bookmark {
    display: block;
    width: 7%;
    margin-top: 6%;
    margin-left: 40%;
    margin-bottom: 6%;
    font-size: 200%;
}

.bookmark:hover {
    cursor: pointer;
}

.linkContainer {
    margin-bottom: 10%;
}

.commentContainer {
    margin-top: 2%;
    margin-bottom: 2%;
    margin-right: 5%;
    padding: 1% 5%;
    background-color: #d4d4d4;
    border-radius: 5px;
}

.addComment {
    width: 94%;
    margin-top: 6px;
    margin-left: 1%;
    border-bottom: 1px solid #efefef;
    padding: 10px 0;
    display: flex;
}

.addComment input {
    background: none;
    border: none;
    height: 16px;
    outline: none;
    font: inherit;
    padding: 0;
    width: 100%;
}

.addComment ::placeholder {
    color: #999;
}

.addComment button {
    cursor: pointer;
}

.addComment button:disabled {
    cursor: auto;
    color: #999;
}

@media only screen and (min-width: 768px) {
    .postContainer {
        display: flex;
    }

    .postInfo {
        margin-left: 1.5%;
        margin-top: 1.5%;
        width: 30%;
    }

    .bookmark {
        margin-left: 1%;
    }

    .infoContainer {
        margin-left: 4%;
    }

    .commentContainer {
        margin-right: 0;
    }

    .addComment {
        width: 98%;
    }
}
